import imageUpload from '@/components/image-upload.vue';
export default {
	components:{
		imageUpload
	},
	
	data() {
		return {
			limitNumber:9,
			enableDrag:true,
			imageData:[],
			title:'',
			content:''
		};
	},
	
	methods:{
		onInputTitle:function(e){
			setTimeout(() => { this.title = e.detail.value }, 0);
		},
		onInputContent:function(e){
			setTimeout(() => { this.content = e.detail.value }, 0);
		},
		sendMsg:function(){
			if(this.imageData.length < 1){
				return uni.showToast({
					title:'请至少上传一张照片',
					icon:'none'
				})
			}
			uni.showLoading({
			  title: '处理中...'
			});
			const data ={
				title: this.title,
				content:this.content,
				images:this.imageData,
				createTime: Date.now()
			}
			uniCloud.callFunction({
			  name: 'addShareMessage',
			  data
			}).then((res) => {
			  uni.hideLoading()
			  uni.showModal({
			    content: `发送成功`,
			    showCancel: false,
				success:(res)=>{
					if(res.confirm) uni.navigateBack()
				}
			  })
			  console.log(res)
			}).catch((err) => {
			  uni.hideLoading()
			  uni.showModal({
			    content: `添加数据失败，错误信息为：${err.message}`,
			    showCancel: false
			  })
			  console.error(err)
			})
		}
	}
}
